import React, {Component} from 'react';
import lodash from 'lodash';
import $ from 'jquery';
import './jquery.tabledit';

export default class Tabledit extends Component {
	table = React.createRef();
	data = []
	componentDidMount(){
		this.data = this.props.data || [];
		this.initTableEdit();
	}
	componentDidUpdate(prevProps, prevState) {
		if(!lodash.isEqual(this.props.data, prevProps.data)) {
			this.data = this.props.data;
			this.initTableEdit();
		}
	}

	initTableEdit() {
	  let self = this;
	  $(this.table.current).Tabledit({
	      editButton: false,
	      deleteButton: false,
	      onAjax: function(action, serialize) {
	      		let row = parseInt(serialize[0].value) -1
	          let item = serialize[1];
	          self.setValue(row, item);
	          return false;
	      },
	      columns: this.props.columns || {identifier: [-1],editable: []}
	  });
	}
	setValue(row, item){
		let data = this.data;
		if(!data[row]){
			data[row] = {};
		}

		data[row][item.name] = item.value;
		if(this.props.update){
			this.props.update(data);
		}
	}
	render() {
		return <table ref={this.table} className={this.props.className} >
			{this.props.children}
		</table>
	}
}